import React, {useState, useCallback} from 'react';
import styled from 'styled-components';
import Header from '../components/Header';
import Footer from '../components/Footer';

const WorkBlock = styled.div`
    .mainImg {
        border-top: 100px solid #fff;
        width: 100%;
        height: 380px;
        position: relative;
        .img {
            width: 100%;
            height: 100%;
            background: url('images/sub_img2.jpg') no-repeat center;
            background-size: cover;
        }
        h1 {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            font-size: 50px;
            color: #fff;
            font-weight: normal;
            text-shadow: 0 0 10px #000;
            &:before {
                content: '';
                position: absolute;
                top: 100%;
                transform: translateY(-50%);
                left: 0;
                right: 0;
                margin: 10px auto 0;;
                width: 100%;
                height: 2px;
                background: #fff;
                box-shadow: 0 0 10px #000;
            }
        }
    }
    .mainContent {
        margin-top: 150px;
        margin-bottom: 150px;
        position: relative;
        .topText {
            p {
                font-size: 18px;
                white-space: wrap;
                line-height: 30px;
                margin: 0 30px;
                &:nth-child(1) {
                    padding: 40px 0;
                }
                &:nth-child(2) {
                    padding-bottom: 40px;
                }
            }
        }
        h2 {
            position: absolute;
            bottom: 100%;
            transform: translate(-50%,-50%);
            left: 50%;
            font-size: 45px;
            font-weight: 300;
            &:before {
                content: '';
                position: absolute;
                top: 100%;
                transform: translateY(-50%);
                left: 0;
                right: 0;
                margin: 10px auto 0;;
                width: 50%;
                height: 2px;
                background: #ddd;
            }
        }
        .bottomText {
            p {
                font-size: 18px;
                white-space: wrap;
                line-height: 30px;
                margin: 0 30px;
                &:nth-child(1) {
                    padding: 40px 0 20px 0;
                }
                &:nth-child(2) {
                    padding-bottom: 40px;
                }
            }
        }
        .parts {
            display: flex;
            flex-direction: column;
            .align {
                display: flex;
                justify-content: center;
            }
            .common {
                width: 360px;
                height: 320px;
                text-align: center;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                border: 1px solid #ddd;
                margin: 15px;
                h3 {
                    font-size: 18px;
                    padding: 20px 0;
                }
                p {
                    font-size: 14px;
                    line-height: 21px;
                }
            }
        }
    }
`;

const Work = () => {

    const [activeTab, setActiveTab] = useState(0);
    const onSelect = useCallback(
            (id) => {
                setActiveTab(id)
            }, []);

    return (
        <div>
            <Header onSelect={onSelect} activeTab={activeTab} />
            <WorkBlock>
                <div className="mainImg">
                    <div className="img"></div>
                    <h1>사업소개</h1>
                </div>
                <div className="mainContent row">
                    <h2>사업분야</h2>
                    <div className="topText">
                        <p>급속하게 진화하고 있는 웹(web) 기반의 테크놀로지(technology), 인터넷을 무선영역으로 확장하는 새로운 무선 데이터 통신 기술을 근간으로 고객 환경에 맞는 최적화된 서비스를 제공하고 있습니다.</p>
                        <p>2019년 매출 30억, 영업이익 3억 6천만원이라는 괄목할 만한 성과를 올렸습니다. 최고의 품질, 기술, 디자인으로 국내 시장에서 고부가가치를 지닌 브랜드로 성장하고 있습니다.</p>
                    </div>
                    <div className="parts">
                        <div className="align depth1">
                            <div className="common part1 ">
                                <img src="images/img1.png" alt="반응형 홈페이지" />
                                <h3>반응형 홈페이지</h3>
                                <p>혁신적인 솔루션을 만나보세요.<br />예술과 기술이 만나 홈 인테리어라는.<br />새로운 개념을 제안합니다.</p>
                            </div>
                            <div className="common part2">
                                <img src="images/img2.png" alt="원페이지 홈페이지" />
                                <h3>원페이지 홈페이지</h3>
                                <p>중소기업에서 대기업까지<br />고객의 비즈니스 환경을 분석해<br />최적화된 비즈니스 환경을 구축합니다</p>
                            </div>
                            <div className="common part3">
                                <img src="images/img3.png" alt="PC+Mobile 홈페이지" />
                                <h3>PC+Mobile 홈페이지</h3>
                                <p>혁신적인 솔루션을 만나보세요.<br />예술과 기술이 만나 홈 인테리어라는.<br />새로운 개념을 제안합니다.</p>
                            </div>
                        </div>
                        <div className="align depth2">
                            <div className="common part4">
                                <img src="images/img4.png" alt="반응형 홈페이지" />
                                <h3>반응형 홈페이지</h3>
                                <p>혁신적인 솔루션을 만나보세요.<br />예술과 기술이 만나 홈 인테리어라는.<br />새로운 개념을 제안합니다.</p>
                            </div>
                            <div className="common part5">
                                <img src="images/img5.png" alt="원페이지 홈페이지" />
                                <h3>원페이지 홈페이지</h3>
                                <p>중소기업에서 대기업까지<br />고객의 비즈니스 환경을 분석해<br />최적화된 비즈니스 환경을 구축합니다</p>
                            </div>
                            <div className="common part6">
                                <img src="images/img6.png" alt="PC+Mobile 홈페이지" />
                                <h3>PC+Mobile 홈페이지</h3>
                                <p>혁신적인 솔루션을 만나보세요.<br />예술과 기술이 만나 홈 인테리어라는.<br />새로운 개념을 제안합니다.</p>
                            </div>
                        </div>                        
                    </div>
                    <div className="bottomText">
                        <p>이와 아울러 중국, 동남아시아를 비롯한 시장 진입에 이어, EU와 미국 시장 진출을 위한 교두보를 세우고 있습니다.</p>
                        <p>이에 만족하지 않고, WORLD FIRST, WORLD BEST의 목표를 향하여 새로운 기술개발과 표준화를 주도하며, 진정한 정보통신분야의 최강자가 되어갈 것입니다.</p>
                    </div>
                </div>
            </WorkBlock>
            <Footer />
        </div>
    );
};

export default Work;