import React from 'react';
import styled from 'styled-components';

const ProductItemBlock = styled.div`
    .common {
        flex: 0 0 25%;
        width: 300px;
        height: 300px;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 50px;
        img {
            width: 100%;
            height: 100%;
        }
        h3 {
            font-size: 12px;
            padding-top: 15px;
            color: #444;
        }
    }
`;

const ProductItem = ({name, photo}) => {
    return (
        <ProductItemBlock>
            <div className="common">
                <img src={photo} alt={name} />
                <h3>{name}</h3>
            </div>
        </ProductItemBlock>
    );
};

export default ProductItem;