import React from 'react';
// import Button from './Button';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const MemberFormBlock = styled.div`
    width: 400px;
    margin: 0 auto;
    h3 {
        font-size: 30px;
        text-align: center;
        margin: 30px 0;
    }
    .gender {
        text-align: left;
        padding-left: 10px;

    }
`;

const StyledInput = styled.input`
    width: 100%;
    padding: 15px 10px;
    border-bottom: 1px solid #555;
    margin-bottom: 20px;
    outline: none;
    &:nth-child(1) {
        margin-top: 30px;
    }
`;

const Button = styled.button`
    width: 100%;
    padding: 15px 10px;
    color: #fff;
    background: #222;
    border: none;
    margin-top: 20px;
    font-size: 14px;
    &:hover {
        background: #555;
    }
`;

const MemberFoot = styled.div`
    color: #222;
    background: #fff;
    border: 1px solid #222;
    margin-top: 20px;
    a {
        display: block;
        width: 100%;
        padding: 15px 10px;
        color: #4394d0;
        font-size: 14px;
        &:hover {
            background: #f5f5f5;
        }
    }
`;

const textType = {
    login: '로그인',
    join: '회원가입'
}

const MemberForm = ({type}) => {
    const text = textType[type]
    return (
        <MemberFormBlock>
            <h3>{text}</h3>
            <form>
                {
                    type === 'join' && <StyledInput type="text" placeholder="이름" name="username" />
                }
                <StyledInput type="text" placeholder="아이디" name="userid" />
                <StyledInput type="password" placeholder="비밀번호" name="password" />
                {
                    type === 'join' && (
                        <>
                            <StyledInput type="password" placeholder="비밀번호 확인" name="passok" />
                            <div className="gender"><label>성별 : </label> 남 <input type="radio" name="gender" value="M" />  여 <input type="radio" name="gender" value="W" /></div>
                        </>
                    )
                }
                <Button type="submit">{text}</Button>
            </form>
            <MemberFoot>
                {
                    type === 'login' ? <Link to="/join">회원가입</Link> : <Link to="/login">로그인</Link>
                }
            </MemberFoot>
        </MemberFormBlock>
    );
};

export default MemberForm;