import React, {useState, useCallback} from 'react';
import styled from 'styled-components';
import Header from '../components/Header';
import Footer from '../components/Footer';
import {Link} from 'react-router-dom';

const CustomerBlock = styled.div`
.mainImg {
    border-top: 100px solid #fff;
    width: 100%;
    height: 380px;
    position: relative;
    .img {
        width: 100%;
        height: 100%;
        background: url('images/sub_img5.jpg') no-repeat center;
        background-size: cover;
    }
    h1 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 50px;
        color: #fff;
        font-weight: normal;
        text-shadow: 0 0 10px #000;
        &:before {
            content: '';
            position: absolute;
            top: 100%;
            transform: translateY(-50%);
            left: 0;
            right: 0;
            margin: 10px auto 0;;
            width: 100%;
            height: 2px;
            background: #fff;
            box-shadow: 0 0 10px #000;
        }
    }
}
.mainContent {
    margin-top: 150px;
    margin-bottom: 150px;
    position: relative;
    h2 {
        position: absolute;
        bottom: 100%;
        transform: translate(-50%,-50%);
        left: 50%;
        font-size: 45px;
        font-weight: 300;
        &:before {
            content: '';
            position: absolute;
            top: 100%;
            transform: translateY(-50%);
            left: 0;
            right: 0;
            margin: 10px auto 0;;
            width: 50%;
            height: 2px;
            background: #ddd;
        }
    }
    table {
        text-align: center;
        position: relative;
        col {
            &:nth-child(1) { width: 5%; }
            &:nth-child(2) { width: 70%; }
            &:nth-child(3) { width: 15%; }
            &:nth-child(4) { width: 10%; }
        }
        th {
            font-size: 16px;
            padding: 10px 0;
            border-top: 2px solid #222;
            border-bottom: 1px solid #999;
            background: #f5f5f5
        }
        td {
            font-size: 14px;
            padding: 20px 0;
            border-bottom: 1px solid #999;
        }
        .td2 {
            text-align: left;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            &:hover { color: #888; }
        }
    }
}
`;

const Community = () => {

    const [activeTab, setActiveTab] = useState(0);
    const onSelect = useCallback(
            (id) => {
                setActiveTab(id)
            }, []);

    return (
        <div>
            <Header onSelect={onSelect} activeTab={activeTab} />
            <CustomerBlock>
                <div className="mainImg">
                    <div className="img"></div>
                    <h1>고객센터</h1>
                </div>
                <div className="mainContent row">
                    <h2>공지사항</h2>
                    <table>
                        <colgroup>
                            <col></col>
                            <col></col>
                            <col></col>
                            <col></col>
                        </colgroup>
                        <tr>
                            <th>번호</th>
                            <th className="th2"><Link to="#">제목</Link></th>
                            <th><Link to="#">작성자</Link></th>
                            <th>작성일자</th>
                        </tr>
                        <tr>
                            <td>1</td>
                            <td className="td2"><Link to="#">첫번째 공지입니다.</Link></td>
                            <td className="td3"><Link to="#">관리자</Link></td>
                            <td className="td4">2021-04-11</td>
                        </tr>
                        <tr>
                            <td>2</td>
                            <td className="td2"><Link to="#">두번째 공지입니다.</Link></td>
                            <td className="td3"><Link to="#">관리자</Link></td>
                            <td className="td4">2021-04-12</td>
                        </tr>
                        <tr>
                            <td>3</td>
                            <td className="td2"><Link to="#">세번째 공지입니다.</Link></td>
                            <td className="td3"><Link to="#">관리자</Link></td>
                            <td className="td4">2021-04-13</td>
                        </tr>
                        <tr>
                            <td>4</td>
                            <td className="td2"><Link to="#">네번째 공지입니다.</Link></td>
                            <td className="td3"><Link to="#">관리자</Link></td>
                            <td className="td4">2021-04-14</td>
                        </tr>
                    </table>
                </div>
            </CustomerBlock>
            <Footer />
        </div>
    );
};

export default Community;