import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import { FaFacebook, FaInstagramSquare, FaYoutubeSquare } from 'react-icons/fa'
import { RiKakaoTalkFill } from 'react-icons/ri'

const FooterBlock = styled.div`
color: #888;
font-size: 14px;
    .snsIcons {
        margin-top: 100px;
        ul {
            display: flex;
            justify-content: center;
            font-size: 38px;
            color: #666;
            li {
                margin: 0 10px;
                &:hover { color: #999; }
            }
        }
    }
    .agreement {
        text-align: center;
        padding: 25px 0 50px;
        a {            
            margin: 0 10px;
            &:hover { color: #000; }
        }
    }
    .info {
        text-align: center;
        margin-bottom: 100px;
        p {
            color: #000;
            padding-top: 20px;
        }
    }
`;

const Footer = () => {
    return (
        <FooterBlock>
            <div className="row">
                <div className="snsIcons">
                    <ul>
                        <li><Link to="#"><FaFacebook /></Link></li>
                        <li><Link to="#"><FaInstagramSquare /></Link></li>
                        <li><Link to="#"><FaYoutubeSquare /></Link></li>
                        <li><Link to="#"><RiKakaoTalkFill /></Link></li>
                    </ul>
                </div>
                <div className="agreement">
                    <Link to="#">개인정보처리방침</Link>
                    <Link to="#">이메일무단수집거부</Link>
                </div>
                <div className="info">
                    <span>주소 : 서울시 중구 태평로 1가 우리빌딩주식회사&nbsp;&nbsp;/&nbsp;&nbsp;</span>
                    <span>대표 : 홍길동&nbsp;&nbsp;/&nbsp;&nbsp;</span>
                    <span>전화 : 02)123-4567&nbsp;&nbsp;/&nbsp;&nbsp;</span>
                    <span>팩스 : 02)123-4567&nbsp;&nbsp;/&nbsp;&nbsp;</span>
                    <span>이메일 : phlink@nate.com</span>
                    <p>Copyrihgt &copy;미광디자인. All rights reserved.</p>
                </div>
            </div>
        </FooterBlock>
    );
};

export default Footer;