const Homesection = ({onSelect, activeTab}) => {

    const settings = {
        dots:true,
        infinite:true,
        speed:600,
        autoplay:true,
        autoplaySpeed:4000,
        slidesToShow:1,
        slidesToScroll:1,
        prevArrow: <IoIosArrowDropleftCircle />,
        nextArrow: <IoIosArrowDroprightCircle />
    };

    const settings2 = {
        dots:false,
        infinite:true,
        speed:600,
        autoplay:true,
        autoplaySpeed:4000,
        slidesToShow:3,
        slidesToScroll:3,
        prevArrow: <IoIosArrowDropleftCircle />,
        nextArrow: <IoIosArrowDroprightCircle />
    };

    return (
        <HomesectionBlock>
            <div className="slideBox1">
                <Slider {...settings}>
                    <div className="slide slide1">
                        <div className="text">
                            <h2>트렌드를 선도하는 기업</h2>
                            <p>Stay hungry, Stay Foolish!</p>
                        </div>
                    </div>
                    <div className="slide slide2">
                        <div className="text">
                            <h2>최고로 가는 최선의 길</h2>
                            <p>Products for your business</p>
                        </div>
                    </div>
                    <div className="slide slide3">
                        <div className="text">
                            <h2>BEST OF YOUR PARTNER</h2>
                            <p>성공적인 비즈니스를 실현해드립니다.</p>
                        </div>
                    </div>
                </Slider>
            </div>
            <div className="article1">
                <div className="innerBox">
                    <div className="title">
                        <h3>B r a n d&nbsp;&nbsp;&nbsp;&nbsp;S t o r y</h3>
                        <p>고객만족 극대화로 최고의 기업이 되겠습니다.</p>
                    </div>
                    <div className="mainBox">
                        <img className="img" src="images/img1.jpg" alt="브랜드 스토리" />
                        <div className="textBox">
                            <p>최고의 IT 서비스 기업임을 자부하고 있는 저희 회사는 지난<br />1999년 설립된 이후, 고객과 사회의 이익을 위해 끊임없이<br />연구하고 노력 발전해왔습니다.</p>
                            <div className="button">
                                <Link to="/Company">회사소개</Link>
                                <Link to="#">연혁</Link>
                                <Link to="#">조직도</Link>
                            </div>
                        </div>
                    </div>
                    <div className="bg"></div>
                </div>
            </div>
            <div className="article2">
                <div className="innerBox">
                    <div className="title">
                        <h3>O u r&nbsp;&nbsp;&nbsp;&nbsp;W o r k</h3>
                        <p>고객만족 극대화로 최고의 기업이 되겠습니다.</p>
                    </div>
                    <div className="bg"></div>
                    <div className="mainBox">
                        <img className="img" src="images/img2.jpg" alt="브랜드 스토리" />
                        <div className="textBox">
                            <p>고객에게 지속적인 양질의 서비스를 제공하는 것을 최고의<br />목표로 하는 저희 회사는, 인터넷을 기반으로 하는 온라인의<br />가상세계를 풍요롭고 쾌적한 환경으로 발전시키고자 합니다.</p>
                            <div className="button">
                                <Link to="/Work">사업분야</Link>
                                <Link to="#">협력업체</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="article3">
                <div className="innerBox">
                    <div className="title">
                        <h3>P r o d u c t</h3>
                        <p>고객만족 극대화로 최고의 기업이 되겠습니다.</p>
                    </div>
                    <div className="mainBox">
                        <div className="slideBox2">
                            <Slider {...settings2}>
                                <div className="slide slide1">
                                    <div className="text">
                                        <h3>히트상품1</h3>
                                        <Link to="/Product" onClick={ ()=>onSelect(0) }>자세히 보기</Link>
                                    </div>
                                </div>
                                <div className="slide slide2">
                                    <div className="text">
                                        <h3>히트상품2</h3>
                                        <Link to="/Product" onClick={ ()=>onSelect(0) }>자세히 보기</Link>
                                    </div>
                                </div>
                                <div className="slide slide3">
                                    <div className="text">
                                        <h3>신제품1</h3>
                                        <Link to="/Product" onClick={ ()=>onSelect(1) }>자세히 보기</Link>
                                    </div>
                                </div>
                                <div className="slide slide4">
                                    <div className="text">
                                        <h3>신제품2</h3>
                                        <Link to="/Product" onClick={ ()=>onSelect(1) }>자세히 보기</Link>
                                    </div>
                                </div>
                                <div className="slide slide5">
                                    <div className="text">
                                        <h3>출시예정1</h3>
                                        <Link to="/Product" onClick={ ()=>onSelect(2) }>자세히 보기</Link>
                                    </div>
                                </div>
                                <div className="slide slide6">
                                    <div className="text">
                                        <h3>출시예정2</h3>
                                        <Link to="/Product" onClick={ ()=>onSelect(2) }>자세히 보기</Link>
                                    </div>
                                </div>
                            </Slider>
                        </div>
                    </div>
                    <div className="bg"></div>
                </div>
            </div>
            <div className="article4">
                <div className="innerBox">
                    <div className="title">
                        <h3>A b o u t&nbsp;&nbsp;&nbsp;&nbsp;U s</h3>
                        <p>고객만족 극대화로 최고의 기업이 되겠습니다.</p>
                    </div>
                    <div className="mainBox">
                        <div className="left">
                            <div className="depth1">
                                <Link to="/Company" className="company">
                                    <p>회사소개</p>
                                    <p>COMPANY</p>
                                    <HiOutlineOfficeBuilding />
                                </Link>
                                <Link to="#" className="directions">
                                    <p>오시는 길</p>
                                    <p>DIRECTIONS</p>
                                    <BsMap />
                                </Link>
                            </div>
                            <div className="depth2">
                                <Link to="#" className="fnq">
                                    <p>자주하는 질문</p>
                                    <p>F &amp; Q</p>
                                    <FiMonitor />
                                </Link>
                                <Link to="#" className="callnum">
                                    <p>문의전화</p>
                                    <p>010.3217.3646</p>
                                    <MdHeadsetMic />
                                </Link>
                            </div>
                        </div>
                        <div className="right">
                            <div className="depth1">
                                <h2>Notice</h2>
                                <div className="depth2">
                                    <table>
                                        <colgroup>
                                            <col></col>
                                            <col></col>
                                        </colgroup>
                                        <tr>
                                            <td className="td1"><Link to="#">· 첫번째 공지사항입니다.</Link></td>
                                            <td className="td2">2021-05-06</td>
                                        </tr>
                                        <tr>
                                            <td className="td1"><Link to="#">· 두번째 공지사항입니다.</Link></td>
                                            <td className="td2">2021-05-07</td>
                                        </tr>
                                        <tr>
                                            <td className="td1"><Link to="#">· 세번째 공지사항입니다.</Link></td>
                                            <td className="td2">2021-05-08</td>
                                        </tr>
                                        <tr>
                                            <td className="td1"><Link to="#">· 게시글의 제목이 길어지는 경우 자동으로 랩핑처리 됩니다.</Link></td>
                                            <td className="td2">2021-05-09</td>
                                        </tr>
                                    </table>
                                </div>
                                <Link className="moreBtn" to="/Customer">+ more</Link>
                            </div>
                        </div>
                    </div>
                    <div className="bg"></div>
                </div>
            </div>
        </HomesectionBlock>
    );
};

export default Homesection;