import React from 'react';
import styled, {css} from 'styled-components';
import item from './item.json';
import ProductItem from './ProductItem.js';

const ProductBlock = styled.div`
.mainImg {
    border-top: 100px solid #fff;
    width: 100%;
    height: 380px;
    position: relative;
    .img {
        width: 100%;
        height: 100%;
        background: url('images/sub_img3.jpg') no-repeat center;
        background-size: cover;
    }
    h1 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 50px;
        color: #fff;
        font-weight: normal;
        text-shadow: 0 0 10px #000;
        &:before {
            content: '';
            position: absolute;
            top: 100%;
            transform: translateY(-50%);
            left: 0;
            right: 0;
            margin: 10px auto 0;;
            width: 100%;
            height: 2px;
            background: #fff;
            box-shadow: 0 0 10px #000;
        }
    }
}
.mainContent {
    margin-top: 150px;
    margin-bottom: 150px;
    position: relative;
    h2 {
        position: absolute;
        bottom: 100%;
        transform: translate(-50%,-50%);
        left: 50%;
        font-size: 45px;
        font-weight: 300;
        &:before {
            content: '';
            position: absolute;
            top: 100%;
            transform: translateY(-50%);
            left: 0;
            right: 0;
            margin: 10px auto 0;;
            width: 50%;
            height: 2px;
            background: #ddd;
        }
    }
    .tabs {
        display: flex;
        justify-content: center;
        text-align: center;

    }
    .parts {
        display: flex;
        flex-wrap: wrap;
        margin: 0 auto;
    }
}
`;

const List = styled.div`
    width: 110px;
    height: 50px;
    line-height: 30px;
    padding: 10px 20px;
    background: #ddd;
    border-radius: 5px;
    cursor: pointer;
    margin: 50px 20px;
    ${
        props => props.active && css`
            background: #333; color: #fff;
        `
    }
`;

const tabTit = [
    { id: 0, title: "히트상품", item: item.hitItem },
    { id: 1, title: "신제품", item: item.newItem },
    { id: 2, title: "출시예정", item: item.comingSoon }
];

const Product = ({onSelect, activeTab}) => {
    return (
        <ProductBlock>
            <div className="mainImg">
                <div className="img"></div>
                <h1>제품소개</h1>
            </div>
            <div className="mainContent row">
                <h2>제품안내</h2>
                <div className="tabs">
                    {
                        tabTit.map( (data) => <List key={data.id} active={ activeTab === data.id } onClick={ () => onSelect(data.id) }>{data.title}</List> )
                    }
                </div>
                <div className="parts">
                    {
                        tabTit[activeTab].item.map( (data, index) => <ProductItem key={index} name={data.name} photo={data.photo} /> )
                    }
                </div>
            </div>
        </ProductBlock>
    );
};

export default Product;