import React, {useState, useCallback} from 'react';
import styled from 'styled-components';
import Header from '../components/Header';
import Footer from '../components/Footer';
import {Link} from 'react-router-dom';

const QuestionBlock = styled.div`
.mainImg {
    border-top: 100px solid #fff;
    width: 100%;
    height: 380px;
    position: relative;
    .img {
        width: 100%;
        height: 100%;
        background: url('images/sub_img4.jpg') no-repeat center;
        background-size: cover;
    }
    h1 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 50px;
        color: #fff;
        font-weight: normal;
        text-shadow: 0 0 10px #000;
        &:before {
            content: '';
            position: absolute;
            top: 100%;
            transform: translateY(-50%);
            left: 0;
            right: 0;
            margin: 10px auto 0;;
            width: 100%;
            height: 2px;
            background: #fff;
            box-shadow: 0 0 10px #000;
        }
    }
}
.mainContent {
    margin-top: 150px;
    margin-bottom: 150px;
    position: relative;
    h2 {
        position: absolute;
        bottom: 100%;
        transform: translate(-50%,-50%);
        left: 50%;
        font-size: 45px;
        font-weight: 300;
        &:before {
            content: '';
            position: absolute;
            top: 100%;
            transform: translateY(-50%);
            left: 0;
            right: 0;
            margin: 10px auto 0;;
            width: 50%;
            height: 2px;
            background: #ddd;
        }
    }
    table {
        text-align: center;
        position: relative;
        col {
            &:nth-child(1) { width: 5%; }
            &:nth-child(2) { width: 70%; }
            &:nth-child(3) { width: 15%; }
            &:nth-child(4) { width: 10%; }
        }
        th {
            font-size: 16px;
            padding: 10px 0;
            border-top: 2px solid #222;
            border-bottom: 1px solid #999;
            background: #f5f5f5
        }
        td {
            font-size: 14px;
            padding: 20px 0;
            border-bottom: 1px solid #999;
        }
        .td2 {
            text-align: left;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            &:hover { color: #888; }
        }
        .listCreate {
            position: absolute;
            top: 100%;
            right: 0;
            margin: 15px 15px 0 0;
            text-align: center;
            a {
                font-size: 14px;
                display: block;
                width: 65px;
                height: 65px;
                line-height: 65px;
                background: #ddd;
                border-radius: 50%;
                &:hover { background: #f5f5f5; }
            }
        }
    }
}
`;

const Question = () => {

    const [activeTab, setActiveTab] = useState(0);
    const onSelect = useCallback(
            (id) => {
                setActiveTab(id)
            }, []);

    return (
        <div>
            <Header onSelect={onSelect} activeTab={activeTab} />
            <QuestionBlock>
                <div className="mainImg">
                    <div className="img"></div>
                    <h1>온라인문의</h1>
                </div>
                <div className="mainContent row">
                    <h2>고객상담</h2>
                    <table>
                        <colgroup>
                            <col></col>
                            <col></col>
                            <col></col>
                            <col></col>
                        </colgroup>
                        <tr>
                            <th>번호</th>
                            <th className="th2"><Link to="#">제목</Link></th>
                            <th><Link to="#">작성자</Link></th>
                            <th>작성일자</th>
                        </tr>
                        <tr>
                            <td>1</td>
                            <td className="td2"><Link to="#">문의드립니다.</Link></td>
                            <td className="td3"><Link to="#">testid1</Link></td>
                            <td className="td4">2021-05-06</td>
                        </tr>
                        <tr>
                            <td>2</td>
                            <td className="td2"><Link to="#">답변요청드립니다.</Link></td>
                            <td className="td3"><Link to="#">idtest2</Link></td>
                            <td className="td4">2021-05-07</td>
                        </tr>
                        <tr>
                            <td>3</td>
                            <td className="td2"><Link to="#">질문드립니다.</Link></td>
                            <td className="td3"><Link to="#">testtest3</Link></td>
                            <td className="td4">2021-05-08</td>
                        </tr>
                        <tr>
                            <td>4</td>
                            <td className="td2"><Link to="#">궁금합니다.</Link></td>
                            <td className="td3"><Link to="#">4test</Link></td>
                            <td className="td4">2021-05-09</td>
                        </tr>
                        <div className="listCreate">
                            <Link to="#">글쓰기</Link>
                        </div>
                    </table>
                </div>
            </QuestionBlock>
            <Footer />
        </div>
    );
};

export default Question;