import React, {useState, useCallback} from 'react';
import styled from 'styled-components';
import Header from '../components/Header';
import Footer from '../components/Footer';

const CompanyBlock = styled.div`
    .mainImg {
        border-top: 100px solid #fff;
        width: 100%;
        height: 380px;
        position: relative;
        .img {
            width: 100%;
            height: 100%;
            background: url('images/sub_img1.jpg') no-repeat center;
            background-size: cover;
        }
        h1 {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            font-size: 50px;
            color: #fff;
            font-weight: normal;
            text-shadow: 0 0 10px #000;
            &:before {
                content: '';
                position: absolute;
                top: 100%;
                transform: translateY(-50%);
                left: 0;
                right: 0;
                margin: 10px auto 0;;
                width: 100%;
                height: 2px;
                background: #fff;
                box-shadow: 0 0 10px #000;
            }
        }
    }
    .mainContent {
        margin-top: 150px;
        margin-bottom: 150px;
        display: flex;
        position: relative;
        div {
            &.left {
                height: 700px;
                flex: 1;
                background: red;
                background: url('images/intro1.jpg') no-repeat center;
            }
            &.right {
                height: 700px;
                flex: 2;
                padding: 50px 0;
                h2 {
                    position: absolute;
                    bottom: 100%;
                    transform: translate(-50%,-50%);
                    left: 50%;
                    font-size: 45px;
                    font-weight: 300;
                    &:before {
                        content: '';
                        position: absolute;
                        top: 100%;
                        transform: translateY(-50%);
                        left: 0;
                        right: 0;
                        margin: 10px auto 0;;
                        width: 50%;
                        height: 2px;
                        background: #ddd;
                    }
                }
                p {
                    line-height: 30px;
                    padding-bottom: 30px;
                    white-space: wrap;
                    span { font-size: 24px; padding-left: 10px; }
                }
            }
        }
    }
`;

const Company = () => {

    const [activeTab, setActiveTab] = useState(0);
    const onSelect = useCallback(
            (id) => {
                setActiveTab(id)
            }, []);

    return (
        <div>
            <Header onSelect={onSelect} activeTab={activeTab} />
            <CompanyBlock>
                <div className="mainImg">
                    <div className="img"></div>
                    <h1>회사소개</h1>
                </div>
                <div className="mainContent row">
                    <div className="left"></div>
                    <div className="right">
                        <h2>인사말</h2>
                        <p>안녕하십니까?</p>
                        <p>저희 회사 홈페이지를 방문하신 고객 여러분을 환영합니다. 회사설립 이후, 어느새 6년이라는 세월이 흘렀습니다. 그동안 고객 여러분께서 회사에 보여주신 관심과 성원에 깊은 감사드립니다. 앞으로도 고객님의 성원에 힘 입어, 더욱 알찬 제품과 서비스를 제공하겠습니다.</p>
                        <p>최고의 IT 서비스 기업임을 자부하고 있는 저희 회사는 지난 1999년 설립된 이후, 고객과 사회의 이익을 위해 끊임없이 연구하고 노력 발전해왔습니다. 고객에게 지속적인 양질의 서비스를 제공하는 것을 최고의 목표로 하는 저희 회사는, 인터넷을 기반으로 하는 온라인의 가상세계를 더욱 풍요롭고 쾌적한 환경으로 창출하고 발전시키고자 합니다.</p>
                        <p>고객 여러분의 목소리에 귀 기울이고 고객 여러분의 기대에 부응하겠습니다. 다시 한번 저희 홈페이지를 찾아주신 고객 여러분께 감사의 말씀을 드립니다. 늘 변함없이 고객과 사회 모두에 기여하는 기업이 되도록 노력하겠습니다.</p>
                        <p>대표이사 <span>박명식</span></p>
                    </div>
                </div>
            </CompanyBlock>
            <Footer />
        </div>
    );
};

export default Company;