import React, {useEffect} from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import { GiHamburgerMenu } from "react-icons/gi";
import { GrClose } from "react-icons/gr";
import { AiOutlineLogin, AiOutlineUserAdd } from "react-icons/ai";
import $ from "jquery";
// import cn from 'classnames';

const HeaderBlock = styled.div`
    width: 100%;
    height: 100px;
    position: fixed;
    background: #fff;
    z-index: 99999;
        .row2 {
            .logo {
                position: absolute;
                top: 50%;
                right: 105%;
                transform: translateY(-50%);
            }
            position: relative;
            height: 100%;
            background: #fff;
            .nav {
                height: 100%;
                display: flex;                
                justify-content: center;
                align-items: center;
                text-align: center;
                position: relative;
                li {
                    position: relative;
                    width: 180px;
                    height: 100%;
                    line-height: 100px;
                    margin: 0 10px;
                    background: #fff;
                    a {
                        display: block;
                        width: 100%;
                        height: 100%;
                        background: #fff;
                        transition: all 0.2s;
                        &:hover { color: #E00C14; }
                    }
                    .depth2 {
                        display: none;
                        position: absolute;
                        top: 100%;
                        background: #ddd;
                        height: 40px;
                        li {
                            line-height: 40px;
                            margin: 0;
                            font-size: 14px;
                            a {
                                background: #fff;
                            }
                        }
                    }
                }
            }
            .quickMenu {
                position: absolute;
                top: 50%;
                left: 105%;
                transform: translateY(-50%);
                font-size: 28px;
                z-indx: 10;
            }
            .openBtn { opacity: 1; display: block; }
            .closeBtn { opacity: 0; display: none; } 
            .loginJoin {
                position: absolute;
                top: 50%;
                right: -40px;
                transform: translateY(-50%);
                font-size: 16px;
                opacity: 0;
                display: none;
                color: #999;
                .Login {
                    display: flex;
                    align-items: center;
                    padding-bottom: 7px;
                    span { padding-left: 10px; }
                    &:hover { color: #4394D0; }
                }
                .Join {
                    display: flex;
                    align-items: center;
                    padding-top: 7px;
                    span { padding-left: 10px; }
                    &:hover { color: #4394D0; }
                }
            }
        }

`;

const Header = ({onSelect, activeTab}) => {

    useEffect( ()=>{
        // 헤더 햄버거버튼 클릭 시 이벤트
        $('.openBtn').on('click', function(){
            $(this).animate({opacity: 0},100,function(){
                $(this).css({display: 'none'})
                $('.closeBtn').css({display: 'block'}).delay(100).stop().animate({opacity: 1},100);
                $('.loginJoin').css({display: 'block'}).stop().animate({opacity: 1, right: '-25px'},100);
            });
        })
        $('.closeBtn').on('click', function(){
            $(this).animate({opacity: 0},100,function(){
                $(this).css({display: 'none'})
                $('.openBtn').css({display: 'block'}).delay(100).stop().animate({opacity: 1},100);
                $('.loginJoin').stop().animate({opacity: 0, right: '-40px'},100,function(){ $(this).css({display: 'none'}) });
            });
        })

        // 헤더에 네비게이션 호버 시 이벤트
        $('.nav > li').hover(
            function(){
                $(this).find('.depth2').stop().slideDown(200);
                $(this).siblings().find('.depth2').stop().slideUp(100);
            },
            function(){
                $(this).find('.depth2').stop().slideUp(200);
            }
        )
    },[] )

    return (
        <HeaderBlock>
            <div className="row2">
                <div className="logo"><Link to="/"><img src="images/logo.png" alt="마광디자인" /></Link></div>
                <ul className="nav">
                    <li>
                        <Link to="/Company">회사소개</Link>
                        <ul className="depth2">
                            <li><Link to="/Company">인사말</Link></li>
                        </ul>
                    </li>
                    <li>
                        <Link to="/Work">사업소개</Link>
                        <ul className="depth2">
                            <li><Link to="/Work">사업분야</Link></li>
                        </ul>
                    </li>
                    <li>
                        <Link to="/Product">제품소개</Link>
                        <ul className="depth2">
                            <li><Link to="/Product" onClick={ ()=>onSelect(0) }>히트상품</Link></li>
                            <li><Link to="/Product" onClick={ ()=>onSelect(1) }>신제품</Link></li>
                            <li><Link to="/Product" onClick={ ()=>onSelect(2) }>출시예정</Link></li>
                        </ul>
                    </li>
                    <li>
                        <Link to="/Question">온라인문의</Link>
                        <ul className="depth2">
                            <li><Link to="/Question">Q &amp; A</Link></li>
                        </ul>
                    </li>
                    <li>
                        <Link to="/Customer">고객센터</Link>
                        <ul className="depth2">
                            <li><Link to="/Customer">공지사항</Link></li>
                        </ul>
                    </li>
                </ul>
                <div className="quickMenu openBtn">
                    <Link to="#"><GiHamburgerMenu className="ham" /></Link>
                </div>
                <div className="quickMenu closeBtn">
                    <Link to="#"><GrClose className="close" /></Link>
                </div>                
                <div className="loginJoin">
                    <Link to="/Login" className="Login"><AiOutlineLogin /><span>Login</span></Link>
                    <Link to="/Join" className="Join"><AiOutlineUserAdd /><span>Join</span></Link>
                </div>
            </div>
            
        </HeaderBlock>
    );
};

export default Header;